@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

/*LOGIN PAGE*/
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Raleway', sans-serif;
    background-image: url("images/Background.jpg");
    background-color: black;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
}

.MainContainer {
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 80vh;
    width: 30vw;
    background: rgba(255, 255, 255, 0.15);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    backdrop-filter: blur(9px);
    border-radius: 10px;
    color: #000;
    text-transform: uppercase;
    letter-spacing: 0.4rem;

}

.Dashboard {
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 95vh;
    width: 95vw;
    background: rgba(255, 255, 255, 0.15);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    backdrop-filter: blur(9px);
    border-radius: 10px;
    color: #000;
    text-transform: uppercase;
    letter-spacing: 0.4rem;

}

.WelcomeText {
    margin: 3rem 0 1rem 0;
    color: #d7d7d7;
}

.InputContainer {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-around;
    height: 20%;
    width: 100%;
}

.ButtonContainer {
    margin: 1rem 0 2rem 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

#name, #password {
    background: rgba(255, 255, 255, 0.15);
    box-shadow: 0 8px 32px 0 rgba(21, 38, 135, 0.37);
    border-radius: 2rem;
    width: 80%;
    height: 3rem;
    padding: 1rem;
    border: none;
    outline: none;
    color: #3c354e;
    font-size: 1rem;
    font-weight: bold;
}

#name:focus, #password:focus {
    display: inline-block;
    box-shadow: 0 0 0 0.2rem #203081;
    backdrop-filter: blur(12rem);
    border-radius: 2rem;
}

#name::placeholder, #password::placeholder {
    color: #00152c;
    font-weight: 100;
    font-size: 1rem;
}

#SubmitButton, #Logout {
    background: linear-gradient(to right, #14163c 0%, #03217b 79%);
    text-transform: uppercase;
    letter-spacing: 0.2rem;
    width: 65%;
    height: 3rem;
    border: none;
    color: #fff;
    border-radius: 2rem;
    cursor: pointer;
}

#Logout {
    position: absolute;
    bottom: 0;
    margin-bottom: 1rem;
    width: 10rem;
}

#RegisterButton {
    background: linear-gradient(to right, #1d4350, #a43931);
    text-transform: uppercase;
    letter-spacing: 0.2rem;
    width: 30%;
    height: 2rem;
    font-size: 0.65rem;
    border: none;
    color: #fff;
    border-radius: 2rem;
    cursor: pointer;
}


.HorizontalRule {
    width: 90%;
    height: 0.3rem;
    border-radius: 0.3rem;
    border: none;
    margin: 1.5rem 0 1rem 0;
    background: linear-gradient(to right, #14163c 0%, #03217b 79%);
    backdrop-filter: blur(25px);
}

.Icon {
    height: 3.5rem;
    width: 3.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4rem;
    color: white;
    cursor: pointer;
}

.Icon-Facebook {
    background: linear-gradient(to right, #0546a0 0%, #663fb6 100%);
}

.Icon-Instagram {
    background: linear-gradient(to right, #d64034 0%, #e5ad14 30%, #349b4e 70%, #3f7adf 100%);
}

.Icon-Twitter {
    background: linear-gradient(to right, #0341CD 0%, #5526BF 50%, #9800BF 100%)
}

svg {
    width: 1.5rem;
    height: 1.5rem;
}

.LoginWith {
    text-align: center;
    color: #6e97c0;
    text-transform: lowercase;
}

.IconContainer {
    display: flex;
    justify-content: space-evenly;
    margin: 2rem 0 3rem 0;
    width: 80%;
}

.ForgotPassword {
    cursor: pointer;
    color: #7e89ce;
    text-transform: capitalize;
    text-align: center;
    letter-spacing: 0.2rem;
    border: none;
    background: none;
    font-size: 16px;
    margin-bottom: 1.5rem;
    font-weight: bold;
}

.error {
    color: darkred;
    font-weight: bolder;
    text-align: center;
}

/*END OF LOGIN PAGE*/



/*REGISTRATION PAGE*/

.WelcomeText-reg {
    margin: 3rem 0 1rem 0;
    color: #d7d7d7;
}

.ButtonContainer-reg {
    margin: 4rem 0 3rem 0;
}

.InputContainer-reg {
    margin-top: 5rem;
}

.HorizontalRule-reg {
    margin: 1rem 0 1rem 0;
}

#name-reg, #password-reg, #email-reg, #confirm-password-reg {
    margin-bottom: 1rem;
    background: rgba(255, 255, 255, 0.15);
    box-shadow: 0 8px 32px 0 rgba(21, 38, 135, 0.37);
    border-radius: 2rem;
    width: 80%;
    height: 3rem;
    padding: 0.8rem;
    border: none;
    outline: none;
    color: #3c354e;
    font-size: 1rem;
    font-weight: bold;
}

#name-reg:focus, #password-reg:focus, #email-reg:focus, #confirm-password-reg:focus {
    display: inline-block;
    box-shadow: 0 0 0 0.2rem #203081;
    backdrop-filter: blur(12rem);
    border-radius: 2rem;
}

#name-reg::placeholder, #password-reg::placeholder, #email-reg::placeholder, #confirm-password-reg::placeholder{
    color: #00152c;
    font-weight: 100;
    font-size: 1rem;
}

/*END OF REGISTRATION PAGE*/



/*SCREEN ADJUSTMENT*/
@media only screen and (max-width: 320px) {
    .MainContainer {
        width: 80vw;
        height: 90vh;
    }

    hr {
        margin-bottom: 0.3rem;
    }

    h4 {
        font-size: small;
    }
}

@media only screen and (min-width: 360px) {
    .MainContainer {
        width: 80vw;
        height: 90vh;
    }

    h4 {
        font-size: small;
    }
}

@media only screen and (min-width: 411px) {
    .MainContainer {
        width: 80vw;
        height: 90vh;
    }
}

@media only screen and (min-width: 768px) {
    .MainContainer {
        width: 80vw;
        height: 80vh;
    }
}

@media only screen and (min-width: 1279px) {
    .MainContainer {
        width: 70vw;
        height: 50vh;
    }
}

@media only screen and (min-width: 1280px) {
    .MainContainer {
        width: 30vw;
        height: 80vh;
    }
}
